.mlp-module-quicklink-settings {
    #position {
        td + td {
            display: flex;

            label {
                display: inline-block;
                margin-left: 10px;
                border: 1px solid #dededef7;
                padding: 10px;
                width: 120px;
                background: #fff;
                color: #000;
                text-align: center;

                &:hover {
                    background: #f7f7f7f7;
                }

                &:before {
                    content: ' ';
                    display: block;
                    margin-bottom: 10px;
                    width: 120px;
                    height: 78px;
                    background: url(../images/quicklinkpositions.png) no-repeat top right #fff;
                }

                &.quicklink-position-top-right:before {
                    background-position: top left;
                }

                &.quicklink-position-bottom-left:before {
                    background-position: bottom left;
                }

                &.quicklink-position-bottom-right:before {
                    background-position: bottom right;
                }
            }
        }
    }
}
